import { AlertController } from '@ionic/angular';
import { ApiCRMService } from 'src/app/services/api-crm.service';
import { StorageService } from 'src/app/services/storage.service';
import { ApiLMSService } from 'src/app/services/api-lms.service';
import { ApiGeneralService } from 'src/app/services/api-general.service';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-course-next',
  templateUrl: './course-next.page.html',
  styleUrls: ['./course-next.page.scss'],
})
export class CourseNextPage implements OnInit {

  AssetsPathSlider = environment.webAssetsPath + "slider-images";
//#region var Reviews
  arrReviews = [];
  slideReviewOpts = {slidesPerView: 1.2}  
  webAssetsPathReviews = "";
  staticIconPath = "https://app.moksh16.in/application-centre/assets/mobility-app/online-coaching/icons/";
//#endregion

  slideFacultyOpts = {slidesPerView: 1.5}
  arrFaculty = [];

  arrUserData = [];
  isLoading : boolean;
  isModalOpen:boolean;
  isSubmitted = false;
  txtCollegeName="";
  ddlSchoolYear="";
  arrResponse = {'status':0};

  constructor(
    private headerTitleService:HeaderTitleService,
    private apiGeneralService : ApiGeneralService,
    private apiLMSService : ApiLMSService,
    private storage : StorageService,
    private apiCRMService : ApiCRMService,    
    private alertController: AlertController
  ) { 
    this.headerTitleService.title="NEXT Coaching";
    this.headerTitleService.isBack=true;
  }

  ngOnInit() { 
    this.getReview();
    this.getFaculty();
    this.getUserData();
  }

  doRefresh(event) {    
    this.getReview();
    setTimeout(() => {event.target.complete()}, 2000);
  }

  getFaculty(){
    var data = new FormData();
    data.append("xAction", "getFacultyList");
    data.append("apiKey", "U0NJVFlSRVdBUkRa");
    this.apiLMSService.getFacultyList(data).then(resp => {
      this.arrFaculty = resp.data;      
    })
    .catch(err => {
        console.log("getFaculty(): " + err);        
      })
  }

  getReview() {   
    let postData = { "Category": "Online Coaching", "IsActive": true }
    this.apiGeneralService.getReviews(JSON.stringify(postData)).then(resp => {
      this.arrReviews = resp;      
      this.webAssetsPathReviews = environment.webAssetsPath + "mobility-app/student-review";      
    })
      .catch(err => {
        console.log("getReview: " + err);       
      })
  }

  onClickFreeTrial(){
    this.isModalOpen = true;
  }
  modalClose() {
    this.isModalOpen = false;    
  }
  onClickFreeTrialSubmit() {
    if(this.arrUserData["leadID"] == 0 || this.arrUserData["leadID"] == '' || this.arrUserData["leadID"] == undefined){
      this.setCRMRawData();
    }
    else{
      this.setCRMLeadData();
    }
    this.isLoading = true;
    let fulName = this.arrUserData["firstName"] + " " + this.arrUserData["lastName"];
    let mobileNo = this.arrUserData["mobileNo"].replace('+91','');
    var postData = 
    {
      'emailID':this.arrUserData["emailId"], 
      'fullname': fulName, 
      'studentMobileNo': mobileNo, 
      'medicalCollegeName':'', 
      'yearOfMedicalSchool':'',
      'programTypeID': '2',
      'batchID' : '43'
    }
    this.apiLMSService.fnSubmitFreeTrial(postData).then(resp=>{
      if(resp == "registered"){
        this.isSubmitted = true;        
      }
      else if (resp == "transfer"){
        this.alertTransfer(postData);
      }
      else if (resp == "exist"){
        this.isModalOpen = false;
      }
      this.isLoading = false;
    });    
  }
  async alertTransfer(obj) {    
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class', header: "Are you sure to change the course?", message: 'On changing of course, your access to the previous course will be stopped', buttons: [
        { text: 'No', role: 'cancel', cssClass: 'secondary'},
        {
          text: 'Yes', handler: () => {
            this.isLoading = true;
            this.apiLMSService.fnTransferCourse(obj).then (resp => {this.isLoading = false});
          }
        }
      ]
    });
    await alert.present();
  }
  setCRMRawData(){
    this.isLoading = true;
    this.apiCRMService.fnGetCRMRawData().then(resp=>{
      if(resp == true){
        this.apiCRMService.arrCRMRawData["lmsCourse"] = this.apiCRMService.enumLMSCourses[4];
        this.apiCRMService.fnSetCRMRawData();       
      }
    });
  }
  setCRMLeadData(){
    this.isLoading = true;
    this.apiCRMService.fnGetCRMLeadData().then(resp=>{
      if(resp == true){
        this.apiCRMService.arrCRMLeadData["lmsCourse"] = this.apiCRMService.enumLMSCourses[4];
        this.apiCRMService.fnSetCRMLeadData();       
      }
    });
  }

  getUserData() {
    this.storage.getObject("userdata").then((resp: any) => {
      this.arrUserData = resp;      
    })
  }
}
